import React, { useEffect, useState } from 'react';
import { BreakType } from '../../App';

type SetupBreakProps = {
    suggestedDuration: number,
    breakType: BreakType,
    startBreak: (description: string, duration: number, breakType: BreakType)=>void,
    cancel: ()=>void
}

export default function SetupBreak(props : SetupBreakProps) {
    const {suggestedDuration, breakType, startBreak, cancel} = props;
    const [description, setDescription] = useState<string>(breakType);
    const [duration, setDuration] = useState(suggestedDuration);

    function handleSubmit(e : React.FormEvent<HTMLFormElement>) {
        e.preventDefault();
        startBreak(description, duration, breakType);
    }

    return(
        <form onSubmit={e => handleSubmit(e)}>
            <label htmlFor="breakDescription" accessKey="c">Des<u>c</u>ription</label>
            <input type="text" id="breakDescription" onChange={(e)=>setDescription(e.target.value)} value={description}/>
            <label htmlFor="breakDuration" accessKey="d"><u>D</u>uration</label>
            <input autoFocus id="breakDuration" type="text" onChange={(e)=>setDuration(parseInt(e.target.value) || 0)} value={duration}/>
            <button accessKey="t" type="submit">S<u>t</u>art</button>
            <button accessKey="b" onClick={cancel}><u>B</u>ack</button>
        </form>
    )
}